<template>
	<view class="login">
		<view class="content">
			<!-- 头部logo -->
			<view class="header">
				<image :src="logoImage"></image>
			</view>
			<!-- 主体表单 -->
			<view class="main">
				<wInput v-model="mobile" type="text" maxlength="11" placeholder="手机号码" :focus="isFocus"></wInput>
				<wInput v-model="password" type="password" placeholder="登录密码"></wInput>
			</view>
			<wButton class="wbutton" text="登 录" :rotate="isRotate" @click="startLogin"></wButton>

			<!-- 其他登录 -->
			<!-- <view class="other_login cuIcon">
				<view class="login_icon">
					<view class="cuIcon-weixin" @tap="login_weixin"></view>
				</view>
			</view> -->

			<!-- 底部信息 -->
			<view class="footer">
				<view @click="forget()">找回密码</view>
			</view>

			<view style="height: 100rpx;"></view>
		</view>
	</view>
</template>

<script>
	import wInput from '../../components/watch-login/watch-input.vue' //input
	import wButton from '../../components/watch-login/watch-button.vue' //button

	var _self
	var code
	export default {
		data() {
			return {
				logoImage: 'data:image/png;base64,',
				mobile: '', //用户/电话
				password: '', //密码
				isRotate: false, //是否加载旋转
				isFocus: true // 是否聚焦
			};
		},
		components: {
			wInput,
			wButton,
		},
		mounted() {

		},
		onLoad() {
			_self = this
			_self.isLogin()

			wx.login({
				success(res) {
					if (res.code) {
						code = res.code
					}
				}
			})
		},
		methods: {
			isLogin() {
				//判断缓存中是否登录过，直接登录
				try {
					const token = uni.getStorageSync('equipment_token');
					const equipmentManage = uni.getStorageSync('equipment_manage');
					if (token) {
						//有登录信息
						getApp().globalData.token = token
						getApp().globalData.equipmentManage = equipmentManage
						const eventChannel = _self.getOpenerEventChannel()
						eventChannel.emit('isLoginFromLogin', {
							isLogin: true
						})

						uni.$emit('loginStatusEvent', {
							isLogin: true
						})
					}
				} catch (e) {
					// error
				}
			},
			startLogin(e) {
				//登录
				if (_self.isRotate) {
					//判断是否加载中，避免重复点击请求
					return false
				}
				if (_self.mobile.length == "") {
					_self.tui.toast('手机号码不能为空')
					return
				}
				if (_self.password.length < 5) {
					_self.tui.toast('登录密码不正确')
					return
				}

				_self.isRotate = true
				setTimeout(function() {
					_self.isRotate = false
				}, 3000)
				uni.showLoading({
					title: '登录中...'
				})
				_self.$api.login({
					code,
					mobile: _self.mobile,
					password: _self.password
				}).then((res) => {
					uni.hideLoading()
					_self.isRotate = false
					_self.tui.toast('登录成功', '', 'success')

					getApp().globalData.token = res.data.token
					getApp().globalData.equipmentManage = res.data.equipment_manage
					uni.setStorageSync('equipment_token', res.data.token)
					uni.setStorageSync('equipment_openid', res.data.openid)
					uni.setStorageSync('equipment_manage', res.data.equipment_manage)
					setTimeout(function() {
						const eventChannel = _self.getOpenerEventChannel()
						eventChannel.emit('isLoginFromLogin', {
							isLogin: true
						})

						uni.$emit('loginStatusEvent', {
							isLogin: true
						})
						uni.navigateBack()
					}, 1500)
				}).catch((err) => {
					uni.hideLoading()
					_self.password = ''
					_self.isRotate = false
					_self.tui.toast(err.msg)
				})
			},
			login_weixin() {
				//微信登录
				uni.showToast({
					icon: 'none',
					position: 'bottom',
					title: '...'
				});
			},
			forget() {
				uni.showModal({
					title: '提示',
					content: '请联系系统管理员',
					showCancel: false,
					confirmText: '知道了',
					confirmColor: '#333333'
				});
			}
		}
	}
</script>

<style>
	@import url("../../components/watch-login/css/icon.css");
	@import url("./css/main.css");

	page {
		background-color: #FFFFFF;
	}
</style>
